:root {
  --color-gray-primary: #666666;
  --color-light-primary: #FFFFFF;
  --color-light-secondary: #F3F3F3;
  --color-dark-primary: #1A1A1A;
  --color-dark-secondary: #232323;
  --color-gray-secondary: #F4F5F7;
  --color-blue-primary: #ffb400;
  --color-blue-secondary: #FFE767;
  --color-blue-third: #CB8000;
  --transition-duration-primary: 0.2s;
  --transition-duration-secondary: 0.5s;
  --transition-function-primary: ease-in-out;
  --transition-function-secondary: cubic-bezier(.4,0,.2,1);
  --border-radius-primary: 6px;
  --border-radius-secondary: 15px;
  --main-font: OpenSans, Helvetica, sans-serif;
  --second-font: PermianSans, Arial, sans-serif;
}

/* Large desktops */

body,
html {
  width: 100%;
  height: 100%;
}

main {
  position: relative;
}

html > div[style] {
  display: none !important;
}

ul,
menu,
ol,
section,
figure,
aside {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

.wrapper {
  position: relative;
  display: block;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 60px;
  overflow: hidden;
}

.page-breadcrumbs {
  margin-bottom: 24px;
  padding: 0;
  font-size: 14px;
}

.page-breadcrumbs a,
.page-breadcrumbs strong {
  margin: 0 10px;
}

.page-breadcrumbs > span > span > a {
  margin-left: 0;
}

.page-breadcrumbs strong {
  margin-right: 0;
}

.stop-scrolling {
  position: fixed;
  width: 100%;
  height: 100%;
  max-height: 100%;
  overflow-y: scroll;
}

section.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

body,
html {
  color: var(--color-gray-primary);
  font-size: 13px;
  font-weight: normal;
  font-family: var(--main-font);
  line-height: 1.3;
  -webkit-font-smoothing: antialiased;
}

h1,
.h1 {
  margin: 26.66667px 0;
  font-size: 23px;
  line-height: 32px;
}

h2,
.h2 {
  margin: 26.66667px 0;
  font-size: 21px;
  line-height: 30px;
}

h3,
.h3 {
  margin: 26.66667px 0;
  font-size: 19px;
  line-height: 24px;
}

h4,
.h4 {
  margin: 26.66667px 0;
  font-size: 17px;
  line-height: 24px;
}

h5,
.h5 {
  margin: 26.66667px 0;
  font-size: 15px;
  line-height: 24px;
}

h6,
.h6 {
  margin: 26.66667px 0;
  font-size: 13px;
  line-height: 20px;
}

.hero__headline {
  margin: 72px 0 26px 0;
  font-size: 24px;
  line-height: 32px;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: var(--second-font);
  padding: 0;
  text-transform: uppercase;
  font-weight: 600;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child,
.h1:first-child,
.h2:first-child,
.h3:first-child,
.h4:first-child,
.h5:first-child,
.h6:first-child {
  margin-top: 0;
  margin-left: 0;
}

h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child,
.h1:last-child,
.h2:last-child,
.h3:last-child,
.h4:last-child,
.h5:last-child,
.h6:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

strong {
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 20px;
}

small {
  margin: 16px 0;
  padding: 0;
  font-size: 13px;
  line-height: 22px;
}

b,
strong,
label,
.label {
  font-weight: 500;
}

p {
  margin: 14px 0;
  padding: 0;
}

p:last-child {
  margin-bottom: 0;
}

a {
  margin: 0;
  padding: 0;
  color: #3a3d45;
}

a,
a:focus,
a:active,
a:hover,
a:visited {
  position: relative;
  color: #666666;
  text-decoration: none;
  transition: color var(--transition-duration-primary) var(--transition-function-primary);
}

/* iPad fix */

input,
textarea,
select {
  -webkit-appearance: none;
  /*disable shaddow nav ipad*/
  -moz-appearance: none;
  /*disable shaddow nav ipad*/
  border-radius: 0;
  /*disable border-radius ipad*/
}

/*
    ----------------------------------
    13. Header content
    ----------------------------------
*/

.header-content {
  padding: 0;
  position: relative;
  overflow: hidden;
}

.header-content .item {
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-position: center;
  background-size: cover;
}

.header-content .item .box {
  position: absolute;
  width: 100%;
  color: #fff;
  top: 50%;
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 99;
}

.header-content .item .title {
  color: white;
  text-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  text-transform: none;
}

.header-content .item .btn {
  margin-top: 40px;
}

.header-content .owl-theme .owl-controls .owl-pagination {
  text-align: end;
  padding-right: 20px;
}

.header-content .owl-theme .owl-controls .owl-buttons div span {
  color: white;
}

.header-content .owl-theme .owl-controls .owl-page span {
  background: white;
}

/*
    ----------------------------------
    44. Owl-icons
    ----------------------------------
*/

.owl-icons-wrapper {
  padding: 0;
  position: relative;
  background: #fff;
}

.owl-icons-wrapper .owl-icons {
  position: relative;
  padding: 5px 0;
}

.owl-icons-wrapper .owl-icons a:hover,
.owl-icons-wrapper .owl-icons figure:hover {
  color: black;
}

.owl-icons-wrapper .owl-icons figure {
  text-align: center;
  padding: 10px 0 20px 0;
}

.owl-icons-wrapper .owl-icons figure:hover .f-icon {
  -moz-transform: scale(1.3);
  -ms-transform: scale(1.3);
  -o-transform: scale(1.3);
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}

.owl-icons-wrapper .owl-icons figure .f-icon {
  font-size: 70px;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.owl-icons-wrapper .owl-icons figcaption {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-size: 12px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
}

.owl-icons-wrapper .owl-icons.owl-theme .owl-controls .owl-buttons div span {
  color: #3a3d45;
}

.owl-icons-wrapper .owl-icons.owl-theme .owl-controls .owl-buttons .owl-next {
  right: -50px;
}

.owl-icons-wrapper .owl-icons.owl-theme .owl-controls .owl-buttons .owl-prev {
  left: -50px;
}

.owl-icons-wrapper .owl-icons-boxed {
  padding: 50px 0;
}

.owl-icons-wrapper .owl-icons-boxed figure {
  background-color: #fafafa;
  border-radius: 4px;
  color: black;
  margin: 0 5px;
}

.owl-icons-wrapper .owl-icons-boxed figure:hover {
  color: #ffbb00;
}

.owl-icons-wrapper.owl-icons-frontpage {
  margin-top: -125px;
  background: transparent;
}

.owl-icons-wrapper.owl-icons-frontpage a,
.owl-icons-wrapper.owl-icons-frontpage a:focus,
.owl-icons-wrapper.owl-icons-frontpage a:active,
.owl-icons-wrapper.owl-icons-frontpage a:hover {
  color: white;
}

.owl-icons-wrapper.owl-icons-frontpage figure:hover,
.owl-icons-wrapper.owl-icons-frontpage .owl-icons.owl-theme .owl-controls .owl-buttons div span {
  color: white;
}

